import React, { useEffect, useState } from 'react';

import '../styles/Nav.scss';
import banner from '../images/banner.png';
import { useNavigate } from 'react-router-dom';

const Nav = () => {

    const [isLogin, setIsLogin] = useState(false);

    let navigate = useNavigate();
    
    const onClickMyPage = () => {
        isLogin ? navigate("/mypage") : navigate("/signin");
    }

    return (
        <div className="Nav">
            <div className="ImgaeWrapper">
                <img src={banner} alt="  " onClick={() => navigate("/")}/>
            </div>
            <div className="PageSelectorWrapper">
                <div id="notice" onClick={() => navigate("/notice")}></div>
                <div id="analysis" onClick={() => navigate("/dataAnalysis")}></div>
                <div id="citizen-participation" onClick={() => navigate("/participation")}></div>
                <div id="data-request" onClick={() => navigate("/dataRequest")}></div>
                <div id="my-page" onClick={() => onClickMyPage()}></div>
            </div>
        </div>
    );
};

export default Nav;